<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="12" md="6">
          <v-card>
            <v-card-title>
              Scan Area
            </v-card-title>
            <v-card-text>
              <v-quagga
                style="height:480px"
                :onDetected="logIt"
                :readerSize="readerSize"
                :readerTypes="['i2of5_reader']"
              ></v-quagga>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" md="6">
          <v-card>
            <v-card-title>
              Results
            </v-card-title>
            <v-card-text>
              <v-list>
                <v-list-item v-for="item in detecteds" :key="item">
                  <v-list-item-content>
                    {{ item.codeResult.code }} - {{ item.codeResult.format }}
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Vue from "vue";
import VueQuagga from "vue-quaggajs";
Vue.use(VueQuagga);

export default {
  name: "barcodeEntry",
  data() {
    return {
      readerSize: {
        width: 640,
        height: 480,
      },
      detecteds: [],
    };
  },
  methods: {
    logIt(data) {
      var self = this;
      self.detecteds.push(data);
      console.log(data);
    },
    onDecode(result) {
      alert(result);
    },
    onError(result) {
      alert(result);
    },
  },
};
</script>
